.page {
  height: 100%;
  background-color: #e5e5e5;
}

.page__body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  background-color: #ffffff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.25);
  margin: 0 auto;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100%;
  text-transform: uppercase;
  text-align: center;
  color: #333333;
  box-sizing: content-box;
}
@media (min-width: 1200px) {
  .page__body {
    max-width: 1200px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.link {
  text-decoration: none;
  color: inherit;
}

.image {
  max-width: 100%;
  object-fit: contain;
  display: block;
}

.main-nav {
  display: flex;
  align-items: center;
  position: relative;
  min-height: 56px;
}

.main-nav__logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0%);
  width: 185px;
  height: 96px;
  z-index: 1;
}
.main-nav__logo:hover, .main-nav__logo:focus, .main-nav__logo:focus-within {
  opacity: 0.6;
  outline: 0;
  outline-offset: 0;
}
.main-nav__logo:active {
  opacity: 0.3;
}

.main-nav__toggle-open {
  padding: 17px 20px 12px;
  display: block;
  border: none;
  background: none;
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
  fill: #81b3d2;
}
@media (min-width: 768px) {
  .main-nav__toggle-open {
    display: none;
  }
}
.main-nav__toggle-open:hover, .main-nav__toggle-open:focus, .main-nav__toggle-open:focus-within {
  opacity: 0.6;
  outline: 0;
  outline-offset: 0;
}
.main-nav__toggle-open:active {
  opacity: 0.3;
}

.main-nav__toggle-close {
  padding: 17px 20px 12px;
  order: 1;
  display: block;
  border: none;
  background: none;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  fill: white;
}
@media (min-width: 768px) {
  .main-nav__toggle-close {
    display: none;
  }
}
.main-nav__toggle-close:hover, .main-nav__toggle-close:focus, .main-nav__toggle-close:focus-within {
  opacity: 0.6;
  outline: 0;
  outline-offset: 0;
}
.main-nav__toggle-close:active {
  opacity: 0.3;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  align-items: center;
  position: absolute;
  top: 0;
  width: 100%;
}
@media (min-width: 320px) {
  .nav-list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    z-index: 2;
  }
}
@media (min-width: 768px) {
  .nav-list {
    flex-direction: row;
    background-color: transparent;
    z-index: 0;
    position: relative;
  }
}

.list-nav--open {
  display: block;
}

.nav-list--close {
  display: none;
}
@media (min-width: 768px) {
  .nav-list--close {
    display: flex;
  }
}

.nav-list--nojs {
  display: block;
  position: relative;
}
@media (min-width: 768px) {
  .nav-list--nojs {
    display: flex;
  }
}
.nav-list--nojs .main-nav__toggle-close {
  display: none;
}

.nav-list__item {
  font-weight: 700;
  min-height: 55px;
  text-align: center;
  color: #ffffff;
  background-color: #81b3d2;
  box-shadow: 0 -1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  font-size: 18px;
  line-height: 26px;
  position: relative;
  width: 100%;
}
@media (min-width: 320px) {
  .nav-list__item:hover, .nav-list__item:focus, .nav-list__item:focus-within {
    background-color: #669ec0;
  }
}
@media (min-width: 768px) {
  .nav-list__item {
    width: 20%;
    color: #000000;
    box-shadow: none;
    font-size: 14px;
    line-height: 21px;
    background-color: transparent;
  }
  .nav-list__item:hover, .nav-list__item:focus, .nav-list__item:focus-within {
    background-color: transparent;
    color: #81b3d2;
  }
}

@media (min-width: 768px) {
  .nav-list__item--active {
    color: #766357;
  }

  .nav-list__item:nth-child(4n+3) {
    margin-left: 20%;
  }
}
.nav-list__item:last-child::after {
  content: "";
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  width: 100%;
  height: 7px;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}
@media (min-width: 768px) {
  .nav-list__item:last-child::after {
    display: none;
  }
}

.nav-list__link {
  padding: 17px 10px 12px;
  box-sizing: border-box;
  display: block;
}
@media (min-width: 320px) {
  .nav-list__link:hover, .nav-list__link:focus, .nav-list__link:focus-within {
    background-color: #669ec0;
    outline: 0;
    outline-offset: 0;
  }
  .nav-list__link:active {
    background-color: #5496bd;
    opacity: 0.3;
  }
}
@media (min-width: 768px) {
  .nav-list__link {
    background-color: transparent;
  }
  .nav-list__link:hover, .nav-list__link:focus, .nav-list__link:focus-within {
    background-color: transparent;
    color: #81b3d2;
  }
  .nav-list__link:active {
    color: rgba(0, 0, 0, 0.3);
  }
}

