.radio {
  appearance: none;
}
.radio:checked ~ .radio__fake::before {
  width: 16px;
  height: 16px;
}

.radio__fake {
  display: flex;
  min-width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: #f2f2f2;
  align-items: center;
  justify-content: center;
  margin-left: -4px;
}
@media (min-width: 768px) {
  .radio__fake {
    margin-left: 0;
  }
}
.radio__fake::before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-radius: 50%;
  background-color: #333333;
}

.form__label:hover .radio__fake {
  background-color: #e5e5e5;
}

.form__label:focus .radio__fake,
.form__label:focus-within .radio__fake {
  background-color: #d5d5d5;
  outline: 0;
  outline-offset: 0;
}

.form__label:active .radio__fake::before {
  background-color: rgba(51, 51, 51, 0.3);
}

.checkbox {
  appearance: none;
}
.checkbox:checked ~ .checkbox__fake::before {
  background-image: url("../img/form/tick.svg");
}

.checkbox__fake {
  display: flex;
  min-width: 46px;
  height: 46px;
  border-radius: 4px;
  background-color: #f2f2f2;
  align-items: center;
