.checkbox {
  appearance: none;

  &:checked ~ .checkbox__fake::before {
    background-image: url("../img/form/tick.svg");
  }
}

.checkbox__fake {
  display: flex;
  min-width: 46px;
  height: 46px;
  border-radius: 4px;
  background-color: rgba(242, 242, 242, 1);
  align-items: center;
  justify-content: center;
  margin-left: -3px;

  &::before {
    content: "";
    display: block;
    width: 22px;
    height: 16px;
  }
}

.form__label:hover .checkbox__fake {
  background-color: rgba(229, 229, 229, 1);
}

.form__label:focus .checkbox__fake,
.form__label:focus-within .checkbox__fake {
  background-color: rgba(213, 213, 213, 1);
  outline: 0;
  outline-offset: 0;
}

.form__label:active .checkbox__fake::before {
  background-image: url("../img/form/tick-active.svg");
}
